import React, { useState, useEffect } from "react";
import deflate from "deflate-js";
import styles from "./styles.module.scss";
import Table from "./Table";
import { parse } from "query-string";

interface Partitions {
  [partition: string]: string[][];
}

const DynamoDBTool: React.FC = () => {
  const [text, _setText] = useState<string>("");

  useEffect(() => {
    const query = parse(window.location.search.toString());
    const text = query.text as string;

    if (text) {
      const decoded = atob(decodeURIComponent(text));

      const numArr = decoded.split("").map(char => char.charCodeAt(0));
      const inflatedText = deflate
        .inflate(numArr)
        .map(num => String.fromCharCode(num))
        .join("");

      _setText(inflatedText);
    } else {
      const fallbackText =
        typeof window !== "undefined"
          ? window.localStorage.getItem("text") || "#pk, $sk"
          : "";
      _setText(fallbackText);
    }
  }, []);

  const [headText, ...rowsText] = text.split("\n");

  const head = headText.split(",").map(cell => cell.trim());
  const rows = rowsText.map(rowText =>
    rowText.split(",").map(cell => cell.trim())
  );

  const setText = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const text = e.target.value;
    _setText(text);
    localStorage.setItem("text", text);
  };

  const [generated, setGenerated] = useState("");

  const generate = () => {
    const small = deflate.deflate(
      text.split("").map(char => char.charCodeAt(0)),
      9
    ) as number[];
    const asciiShit = small.map(num => String.fromCharCode(num)).join("");
    const encoded = btoa(asciiShit);

    const uriSafe = encodeURIComponent(encoded);

    setGenerated(uriSafe);
  };

  return (
    <div className={styles.dynamodb}>
      <h2>DynamoDB designer tool</h2>
      <textarea className={styles.tarea} value={text} onChange={setText} />
      <Table head={head} rows={rows} />
      <div className={styles.share} onClick={generate}>
        Share this design with link
      </div>
      {generated.length > 0 && (
        <div className={styles.generated}>
          <h3>Copy this link and share the design with others</h3>
          <hr />
          https://martinschnurer.github.io/calculators/dynamodb-designer?text=
          {generated}
        </div>
      )}
      <h3>How it works</h3>
      <p>
        NoSQL Workbench from AWS works really well. However, I found the process
        of setuping up tables unnecessary hard.
      </p>
      <p>
        I came with the idea, that the table and schema would be generated from
        the text input. You can set up your primary key and other GSI indexes.
        Also, you can click on these indexes and aggregate data into partitions.
      </p>
      <h3>Usage:</h3>
      <ul>
        <li>
          By default <b>primary key</b> is always the first column (without any
          sortkey).
        </li>
        <li>
          Columns are always defined in the first line of the text such as "PK,
          SK, type" will generate three columns with a name <i>PK</i>, <i>SK</i>{" "}
          and <i>type</i>
        </li>
        <li>
          Text "#pk, $sk" will create column - primary key with name <b>#pk</b>{" "}
          and connected sort key <b>$sk</b> to this primary key
        </li>
        <li>
          You can create <b>global secondary index</b> with text{" "}
          <b>"##pk2, $$sk2"</b>. The most important part is the count of the #
          and $, because they are matching together with the cound of these
          characters inside the name
        </li>
        <li>
          If you want to create more GSIs, just use more of these:
          <br />
          ##pk1, $$sk1, ###thirdPK, $$$thirdSK, ####fourthXYZ, $$$$fourth_SK
        </li>
        <li>
          <h4>
            Try this example: <br />
            <small>(Copy and paste to the input above)</small>
          </h4>

          <pre className={styles.stylized}>
            #pk, $sk, ##pk1, $$sk2, info
            <br />
            User-A, INFO, score, 3, Additional information
            <br />
            User-A, Connection
            <br />
            User-B, INFO, score, 4, Additional information
            <br />
            User-C, INFO, score, 5, Additional information
            <br />
            User-D, INFO, score, 1, Additional information
            <br />
          </pre>
          <p>
            Table is created with your data and indexes. Take a look at header
            of the table. The primary key and GSI indexes are clickable. By
            clicking on them, you activate aggregating data by this index (also
            sort by the appropriate sort key). With this way, you can explore
            access patterns much easier.
            <br /> Pretty cool hah?
          </p>
        </li>
      </ul>

      <h3>What's missing:</h3>
      <ul>
        <li>Items can be sorted by sort key only by string type for now</li>
        <li>
          For now, it can't sort items in table by another sort key (local
          secondary index)
        </li>
      </ul>
    </div>
  );
};

export default DynamoDBTool;
